.movies-list {
  width: 100vw;
  padding-top: 1rem;
  text-align: center;
}
.movies-list > p {
  margin: 0 0 1rem 0;
  font-size: 3rem;
  font-weight: bold;
  text-decoration: 0.2rem red solid line-through;
}
.movies-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}
.movie-card {
  position: relative;
  width: 12rem;
  aspect-ratio: 1/2;
}
.movie-card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.movie-card-vote-avg,
.movie-card-title {
  color: white;
  position: absolute;
  width: 100%;
}
.movie-card-title {
  bottom: 3rem;
}
.movie-card-vote-avg {
  bottom: 1rem;
  overflow: hidden;
}
.add-to-fav {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: white;
}
.add-to-fav > span {
  font-size: 2rem;
}
.add-to-fav:active {
  color: red;
}
.pagination {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.pagination > button {
  border: none;
  outline: none;
  background-color: royalblue;
  color: white;
  width: 3rem;
  border-radius: 2rem;
  height: 2rem;
}
.pagination > button:active {
  opacity: 0.7;
}
