.navbar {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: black;
  height: 9vh;
}
#navbar-movie {
  color: red;
  font-weight: bold;
  font-size: 2.5rem;
  position: absolute;
  left: 10vw;
  top: 50%;
  text-shadow: 6px 1px 4px rgba(255, 255, 255, 0.3);
  transform: translateY(-50%);
}
#navbar-movie sup {
  font-size: 1rem;
  color: white;
}
#navbar-fav {
  margin-right: 10vh;
}
#navbar-fav > span {
  color: yellow;
  cursor: pointer;
  font-size: 2.2rem;
}
#navbar-fav > span:active {
  opacity: 0.6;
}

@media screen and (max-width: 480px) {
  #navbar-movie {
    top: 2.5rem;
    left: 8.5rem;
  }
  #navbar-fav {
    margin-right: 5vw;
  }
  #navbar-fav span {
    font-size: 2rem;
  }
}
