.banner {
  width: 100vw;
  height: 80vh;
  background-color: lightblue;
  position: relative;
}
.banner-img {
  height: 100%;
  width: 100%;
  object-fit: fill;
}

#next > span,
#prev > span {
  font-size: 3rem;
  color: whitesmoke;
  cursor: pointer;
}
#next,
#prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#prev {
  left: 2rem;
}
#next {
  right: 2rem;
}
.banner-title,
.banner-overview {
  color: white;
  position: absolute;
  left: 1rem;
}
.banner-title {
  font-size: 3rem;
  bottom: 2.5rem;
  font-weight: bold;
}
.banner-overview {
  font-size: 1.2rem;
  bottom: 1rem;
  height: 2rem;
  width: 90vw;
  display: flex;
  overflow: hidden;
}
@media screen and (max-width: 480px) {
  .banner {
    height: 70vh;
  }
  .banner-img {
    object-fit: cover;
  }
}
